<template>
  <db-welcome />
</template>

<script>
  import WebFont from 'webfontloader';
  import dbWelcome from './Welcome';

  WebFont.load({
    google: {
      families: ['Roboto'],
    },
  });

  export default {
    components: {
      dbWelcome,
    },
  };
</script>

<style lang="scss">
  html {
    box-sizing: border-box;
    font-family: 'Roboto', Helvetica Neue, sans-serif;
  }

  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }

  .fa-icon {
    box-sizing: content-box;
  }

  body,
  html {
    margin: 0;
    padding: 0;
    height: 100vh;
  }

  .grid {
    display: flex;
    flex-flow: row nowrap;
    padding: 1rem;

    &.grid--justify-center {
      justify-content: center;
    }

    &.grid--bleed {
      padding: 0;
    }
  }

  [class*="grid__col-"] {
    margin-right: 1rem;
    padding: 1rem;

    &:last-child,
    &:only-child {
      margin-right: 0;
    }

    &.grid__col-auto {
      flex: 1 1;
    }

    &.grid__col--align-self-center {
      align-self: center;
    }

    &.grid__col--bleed-x {
      padding: 1rem 0;
    }

    &.grid__col--bleed {
      padding: 0;
    }
  }

  @media (max-width: 400px) {
    .grid {
      flex-flow: column nowrap;
    }
  }

  @media (min-width: 400px) {
    .grid__col-sm-8 {
      width: 75%;
    }

    .grid__col-sm-6 {
      width: 50%;
    }

    .grid__col-sm-4 {
      width: 33.333333333%;
    }

    .grid__col-sm-2 {
      width: 16.6666666667%;
    }
  }
</style>



